:root{
  --secondary-bg-color: rgba(190, 212, 92, 0.793);
}
html,
body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  
 
}
  #root {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    overflow:visible;
    height: 100%;
    
    
  }
  
  .App {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    position: relative;
    width: 100%;
    flex-wrap: wrap ;
    height: 100%;
    max-width: 1500px;
    min-width: 360px;
   
    /* background-image: url('./images/wave-haikeiBackground.png'); */
    
  }
