.navbar__menus{
  height: 50vh;
      position: fixed;
      width: 80%;
      top: 20px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      gap: 15px;
      padding: 0;
      background-color: rgb(0,0,0, 0.9);
}

.menu-item{
  position: relative;

}

.navbar__menus h3{
  cursor: pointer;
  font-weight: 500;
  font-size: 14px;
  height: 40px;
  width: 100%;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
 
  
  

}

.navbar__menus h3::after{
  content: " ";
  background-color:white;
  position: absolute;
  height: 1px;
  transform: translateY(15px);
  width: 0px;
  transition: width 0.3s ease-in-out;
  
}
.navbar__menus h3:hover::after{
  width: 100px;

}
