:root{
    --main-bg-color: rgb(198, 218, 108) ;
    --main-txt-color: #4c7009;
 }
.aktuelles{
    width: 80%;
    height: 420px;
    border-top-right-radius: 320px;
    margin-top: -250px;
    border: 1px var(--main-bg-color) solid;
    /* background-image: url('../images/background.avif');
    background-repeat: no-repeat;
    background-size: cover; */
    border-right: 0;


}


.aktuelles__cards {
    display: flex;
    width: 125%;
    height: 250px; /* Set the height to 100% to occupy the full height of the container */
    gap: 20px;

  }


  
  .aktuelles__cards-card {
    width: min(220px, 250px, 320px); /* Set a minimum width for the cards to prevent them from becoming too narrow */
    height: min(200px, 250px, 300px); /* Set the height to 100% to occupy the full height of the container */
    border: 0.5px solid green;
    border-radius: 15px;
    display: flex;
    flex-direction: column-reverse;
    justify-content: space-around;
    align-items: center;
    padding: 0;
    margin-left: 20px;
    
   
    
  }
  .aktuelles__cards-card:hover {
    background-color: rgba(255, 255, 255, 0.329);

  }
  .aktuelles__cards-card-image{
    width: 100%;
    height: 80%;
    overflow: hidden;
    margin: 0;
    padding: 0;
    z-index: -1;
  }
  .aktuelles__cards-card-image img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border: 0.2 solid grey;
    transition: 0.5s ease-in;
  }
  .aktuelles__cards-card-image img:hover{
    transform: scale(1.01);
  
  }
  
  
.aktuelles__cards-card-info{
    width: 100%;
    text-align: center;
    height: 20%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 0;
    overflow: hidden;

}
.aktuelles__cards-card-info h2{
 font-size: 17px;
 margin: 10px 0 5px 0;
 padding: 0;
 width: 100%;
 font-stretch: extra-expanded;
}
.aktuelles__cards-card-info h4{
    font-size: 15px;
    margin: 0;
    padding: 0;
    width: 100%;
    font-weight: 200;
   }
.aktuelles__cards-card-info p{
    font-size: 11.5px;
    padding: 0;
    margin-top: 10px;
    width: 100%;
    line-height: 16px;
    font-family: 'Roboto', sans-serif;
    font-weight: 100;
    font-stretch:extra-expanded;
   }


   @keyframes slideCards {
    0%{
        transform: translateX(0);
    }
    50%{
        transform: translateX(calc(-100% - 20px * 6)); 
    }
    100%{
        transform: translateX(0 ); 
    }
    
   }
   .aktuelles__title{
    /* background-image: url('../images/blob-haikeoutline.png'); */
  
   height: 200px;
    
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content:space-around;
    align-items: center;
    padding: 0;
    box-sizing: border-box;
    width: 100%;
    
   }
 
   .aktuelles__title h1{
    width: 300px;
    font-weight: 600;
    font-size: 35px;
    font-family: 'Poppins', sans-serif;
    text-transform: capitalize;
    margin: 0;
    padding: 0;
    text-align: left;
    height: 100%;
    margin-left: 25px;
    line-height: 50px;
    color: var(--main-txt-color);
    



   }
   
   .aktuelles__title p{
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    font-size: 14px;
    padding:0px;
    margin: 15px;
    width: 480px;
    margin-right: auto;
    
   }