footer{
    width: 100%;
    background-image: url('../images/footerBack.png');
    height: 200px;
    background-position: center bottom;
    background-repeat: no-repeat;
    background-size: 100%;
    background-color: white;
    margin-top: 30px;
    display: flex;
    flex-direction: row;
    justify-content:space-evenly;
    align-items: flex-start;
    gap: 0px 0px;
    flex-wrap: wrap;
    padding-top: 10px;
    padding-bottom: 20px;
    border-top: 0.5px solid rgb(65, 101, 67);
    background-color: rgba(152, 225, 116, 0.182);

}
.detail-holder{
    
    flex: 1 0 20%; /* By default, each item occupies 1/3 of the container width */
    
    height: 200px;
    padding: 10px;
    box-sizing: border-box;
  
    text-align: left;
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
    display: flex;
    flex-direction: column;
    border-radius: 40%;
    margin-right: 10px;
    margin-left: 10px;

}
.detail-holder p{
    font-size: 12px;
    text-align: justify;
    margin-top: 0;
}
.detail-holder h5{
    border-bottom: 0.9px solid rgb(101, 151, 76);
    padding-bottom: 10px;
    width: 100%;
    margin-bottom: 10px;

    
    
}
.detail-holder h5:hover{
    border-bottom: 0.9px solid rgb(101, 151, 76);
    padding-bottom: 10px;
    
}



@media (max-width: 768px) {
    /* On tablets and smaller devices, each item occupies 1/2 of the container width */
    .detail-holder {
      flex: 1 0 50%;
    }
    footer{
        width: 100%;}
  }
  
  @media (max-width: 480px) {
    /* On mobile devices, each item occupies 100% of the container width */
    .detail-holder {
      flex: 1 0 100%;
    }
    footer{
        width: 100%;}
  }
