@import url('https://fonts.googleapis.com/css2?family=Agdasima:wght@400;700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
:root{
    --main-bg-color: rgb(198, 218, 108) ;
    --main-txt-color: #4c7009;
    --secondary-bg-color: rgba(190, 212, 92, 0.793);
 }
.info{
    width: 70%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: 'Poppins', sans-serif;
    background-image: url('../images/mission-Back.svg');
    background-position: left bottom;
    background-size: cover;

   
}
.info__title{
    width: 90%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-top: 25px;

}
.info__title-icon-left{
    font-size: 45px;
    margin: 0;
    padding: 0;
}
.info__title-icon-right{
    font-size: 45px;
    margin-top: 55px;
    padding: 0;
    
}
.info__title h1{
    font-size: 35px;
    text-align: center;
    font-weight: 600;
    margin: 20px 10px 10px 20px;
    padding: 0;
    text-align: center;
    color: var(--main-txt-color);

}

.info__services{
    width: 100%;
    height: 250px;
    display: flex;
    justify-content: space-evenly;
    
    margin-top: 50px;
}

.info__services-item{
    width: 30%;
    
    height: 290px;
    background-color: rgba(190, 212, 92, 0.793);
    display: flex;
    flex-direction: column;
    gap: 0px;
    justify-content: space-evenly;
    align-items: center;
    border-radius: 10px;
    

}
.info__services-item i{
    font-size: 40px;
    background-color: white;
    object-fit: cover;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
    margin-bottom: 0;
    padding-bottom: 0;
   
}
#hiking :nth-child(1){
    margin-bottom: 0;
    padding-bottom: 0;
    color: rgb(41, 127, 41);
    height: 60px;
    margin: 20px 0;

}

#heart :nth-child(1){
    color: rgb(247, 39, 39);
    height: 60px;
    margin: 20px 0;
}
#hand :nth-child(1){
    color: rgb(104, 58, 232);
    height: 60px;
    margin: 20px 0;
}
p{
    font-size: 11px;
    
    margin: 10px 15px;
    height: 100%;
}
h2{
    height: 40px;
    font-size: 15px;
    padding: 0;
    margin: 0 0 10px 0;
    font-family: 'Poppins', sans-serif;
    font-weight: 500;
}

@media only screen and (max-width: 844px) {
   
    .info{
      width: 100%;
    }
  }