 .mission__images{
   width: 46.7%;
  height: 100%;
  background-color: var(--main-bg-color);
 
  display: grid;
  grid-template-columns: repeat(10, 1fr); /* 4 columns */
  grid-template-rows:repeat(10, 1fr); /* 4 rows */
  overflow: hidden;
  
   
   }
   .mission__images img{
      height: 100%;
      width: 100%;
      object-fit: cover;
   }

 #item-old1{
   
   grid-row: span 2;
   grid-column: span 7;
 }
 #item-old2{
  
   grid-row: span 1;
   grid-column: span 3;
 }
 #item-old3{
   grid-row: span 3;
   grid-column: span 3;

 }
 #item-old4{
   grid-row: span 5;
   grid-column: span 4;

 }
 #item-old5{
   grid-row: span 3;
   grid-column: span 3;

 }

 #item-old6{
   grid-row: span 2;
   grid-column: span 2;
 }
 #item-old7{
   grid-row: span 6;
   grid-column: span 1;
 }
 #item-old8{
   grid-row: span 1;
   grid-column: span 1;
 }
 #item-old9{
   grid-row: span  1;
   grid-column: span 2;
 }
 #item-old10{
   grid-row: span 4;
   grid-column: span 5;
 }
 #item-old11{
   grid-row: span 3;
   grid-column: span 4;
 }

 
