 .mission__title{
    width: 50%;
    margin-left: auto;
    
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
    padding-bottom: 0;
    gap: 0;

 }

 .mission__title h1{
    font-size: 45px;
    font-weight: 400;
    margin-bottom: 20px;
    color: #4c7009;
    text-align: center;
    font-family: 'Poppins', sans-serif;

 }
 .mission__title p{
    margin-left: auto;
    width: 100%;
    margin-top: 0;
    margin-bottom: 20px;
    font-family: 'Poppins', sans-serif;
    font-size: 15px;
     }
 .mission__title button{
    width: 120px ;
    
    background-color: #4c7009;
    color: #ffffff;
    padding: 10px 0;
   border-radius: 2px;
    font-weight: 400;
    font-size: 10px;

    border: 0;
    font-family: 'Poppins', sans-serif;
    margin-left: auto;
    margin-right: 20px;
    
    cursor: pointer;
    box-shadow: 2px 2px 2px rgb(0, 0, 0, 50);
    transition: transform 0.1s ease-in-out, box-shadow 0.1s ease-in;
 }
 .mission__title button:active{
  
    transform: translate(2px, 2px);
    box-shadow: none;
 }
 



  